import React from 'react'
import { useTranslation } from 'react-i18next'
import { useWeapon, useWeaponActions } from '~/app/hooks/weapon'
import Select from '../common/Select'

require('./WeaponSlots.css')

interface Props {
}

const WeaponSlots: React.FC<Props> = () => {
  const { t } = useTranslation()
  const { slots, skill } = useWeapon()
  const { setSlots } = useWeaponActions()
  const value = [...slots, skill === 'yws_none' ? 0 : skill === 'yws_' ? 2 : 1].join('-')

  const onChange = (e: React.ChangeEvent<HTMLSelectElement>) =>
    setSlots(e.currentTarget.value.split('-').map(Number) as [number, number, number, 0 | 1])

  return (
    <div className="WeaponSlots">
      <div>{t('')}</div>
      <Select value={value} onChange={onChange}>
        <optgroup label={t('')}>
          <option value="0-0-0-0">{t('')}</option>
        </optgroup>
        <optgroup label={t('Lv{{level}}', { level: 1 })}>
          <option value="1-0-0-0">1</option>
          <option value="1-1-0-0">11</option>
          <option value="1-1-1-0">111</option>
        </optgroup>
        <optgroup label={t('Lv{{level}}', { level: 2 })}>
          <option value="2-0-0-0">2</option>
          <option value="2-1-0-0">21</option>
          <option value="2-1-1-0">211</option>
          <option value="2-2-0-0">22</option>
          <option value="2-2-1-0">221</option>
          <option value="2-2-2-0">222</option>
        </optgroup>
        <optgroup label={t('Lv{{level}}', { level: 3 })}>
          <option value="3-0-0-0">3</option>
          <option value="3-1-0-0">31</option>
          <option value="3-1-1-0">311</option>
          <option value="3-2-0-0">32</option>
          <option value="3-2-1-0">321</option>
          <option value="3-2-2-0">322</option>
          <option value="3-3-0-0">33</option>
          <option value="3-3-1-0">331</option>
          <option value="3-3-2-0">332</option>
          <option value="3-3-3-0">333</option>
        </optgroup>
        <optgroup label={t('Lv{{level}}', { level: 4 })}>
          <option value="4-0-0-0">4</option>
          <option value="4-1-0-0">41</option>
          <option value="4-1-1-0">411</option>
          <option value="4-2-0-0">42</option>
          <option value="4-2-1-0">421</option>
          <option value="4-2-2-0">422</option>
          <option value="4-3-0-0">43</option>
          <option value="4-3-1-0">431</option>
          <option value="4-3-2-0">432</option>
          <option value="4-3-3-0">433</option>
          <option value="4-4-0-0">44</option>
          <option value="4-4-1-0">441</option>
          <option value="4-4-2-0">442</option>
          <option value="4-4-3-0">443</option>
          <option value="4-4-4-0">444</option>
        </optgroup>
        <optgroup label={t('')}>
          <option value="0-0-0-2">{t('[]')}</option>
          <option value="3-0-0-2">{t('[]')}3</option>
        </optgroup>
        <optgroup label={t('')}>
          <option value="4-0-0-1">{t('[]')}4</option>
          <option value="4-1-0-1">{t('[]')}41</option>
          <option value="4-1-1-1">{t('[]')}411</option>
          <option value="4-2-0-1">{t('[]')}42</option>
          <option value="4-2-1-1">{t('[]')}421</option>
          <option value="4-2-2-1">{t('[]')}422</option>
          <option value="4-3-0-1">{t('[]')}43</option>
          <option value="4-3-1-1">{t('[]')}431</option>
          <option value="4-3-2-1">{t('[]')}432</option>
          <option value="4-3-3-1">{t('[]')}433</option>
          <option value="4-4-0-1">{t('[]')}44</option>
          <option value="4-4-1-1">{t('[]')}441</option>
          <option value="4-4-2-1">{t('[]')}442</option>
          <option value="4-4-3-1">{t('[]')}443</option>
          <option value="4-4-4-1">{t('[]')}444</option>
        </optgroup>
      </Select>
    </div>
  )
}

export default WeaponSlots
